
html {
  font-size: calc(0.75rem + ((1vw - 3.2px) * 0.125));
  min-height: 0vw;
}
body {
  padding: 5px;
  width: 100%;
}
label.garupa {
  color: white;
  background: #fc3f73;
  border-radius: 15px;
  padding: 0 15px;
  margin: 10px 0;
  display: inline-table;
  font-size: 1rem;
}
select.garupa {
  background: #dcdcdc;
  width: 100%;
  border-radius: 4px;
  padding: 0 5px;
}
select {
  font-size: 1rem;
  -webkit-box-shadow: inset 1px 1px #333;
          box-shadow: inset 1px 1px #333;
}
input[type=text].garupa {
  background: #dcdcdc;
  -webkit-box-shadow: inset 1px 1px #333;
          box-shadow: inset 1px 1px #333;
}
textarea.garupa {
  background: #dcdcdc;
  -webkit-box-shadow: inset 1px 1px #333;
          box-shadow: inset 1px 1px #333;
}
hr {
  background: #fc3f73;
  margin-top: 10px;
  margin-bottom: 5px;
}
.horizontal {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.for-checkbox {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 0 5px;
}
.button.garupa {
  background: #fc3f73;
  margin: 5px;
}
div.ranking-container {
  display: inline-block;
  width: 100%;
  max-width: 600px;
  border-radius: 5px;
  border: solid 1px;
  margin: 5px 0;
  padding: 5px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
div.ranking-rank {
  width: 80px;
  margin: 0 5px;
}
div.ranking-team-score {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  font-size: 0.7rem;
}
div.ranking-name-rating {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  border-bottom: dotted;
  border-color: #CCC;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
div.ranking-name {
  font-size: 1rem;
  font-weight: bold;
}
div.ranking-score {
  font-size: 1rem;
  color:#fff;
  background:#777777;
  border-radius:15px;
  padding: 0 15px;
  margin: 1px 0;
  width: 100px;
  vertical-align: center;
}
.ribbon {
  display: inline-block;
  position: relative;
  height: 20px;
  line-height: 20px;
  text-align: center;
  padding: 0 20px;
  font-size: 1rem;
  background: #ff6fa3;
  color: #FFF;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.ribbon:before, .ribbon:after {
  position: absolute;
  content: '';
  width: 0px;
  height: 0px;
  z-index: 1;
}
.ribbon:before {
  top: 0;
  left: 0;
  border-width: 10px 0px 10px 8px;
  border-color: transparent transparent transparent #fff;
  border-style: solid;
}
.ribbon:after {
  top: 0;
  right: 0;
  border-width: 10px 8px 10px 0px;
  border-color: transparent #fff transparent transparent;
  border-style: solid;
}
p.titleheader {
  word-break: keep-all;
  font-size: 1.5rem;
  font-weight: bold;
}
table.participation {
  font-size: 0.6rem;
}
